let React=require("react");
var validation=require("../Lang/validation.js");
var regexp=require("../Lang/regs.js");
let Validate={
    validate:function(value) {
        if(value==null||value==undefined)
        {
            value=this.state.value;
        }

        var isvalidate=true;
        var readonly=this.state.readonly;
        var required=this.state.required;
        var helpTip="";
        if(readonly) {//
        }
        else {//
            if(this.state.validateState&&this.state.validateState!="valid")
            {//,
                isvalidate=false;
            }
            else
            {//
                if (value!=null&&value!=undefined && value !== "") {//

                    //0
                    if(value.toString()=="NaN")
                    {//
                        isvalidate=false;
                        helpTip="";
                    }
                    else if(typeof value==="string"&&(value.indexOf("alert(")>-1||value.indexOf("<script>")>-1||value.indexOf("--")>-1)) {//TODO 
                        isvalidate = false;
                        helpTip = "";
                    }
                    else if (this.props.regexp && this.props.regexp !== "") {  //

                        isvalidate = this.props.regexp.test(value);
                        if (!isvalidate) {//

                            if (!this.props.invalidTip && this.props.invalidTip !== "") {//
                                helpTip = this.props.invalidTip;
                            }
                            else {//
                                helpTip = validation["invalidTip"];
                            }
                        }
                        else {//
                        }
                    }
                    else {//

                        if(this.props.type=="date") {//

                            isvalidate= regexp.date.test(value)||regexp.datetime(value);
                        }
                        else if (regexp[this.props.type]) {

                            if (typeof regexp[this.props.type] == "function") {
                                isvalidate = regexp[this.props.type](value);
                            }
                            else {
                                isvalidate = regexp[this.props.type].test(value);
                            }


                            if (!isvalidate) {
                                helpTip = validation[this.props.type];
                            }
                        }
                        else {

                        }
                    }

                    if(!isvalidate)
                    {//
                        //
                        if (this.state.min!=null&&this.state.min!=undefined) {
                            switch (this.props.type) {
                                case "text":
                                    if (value.toString().length < this.state.min) {
                                        isvalidate = false;
                                        helpTip = "" + this.state.min;
                                    }
                                    break;
                                case "password":
                                    if (value.toString().length < this.state.min) {
                                        isvalidate = false;
                                        helpTip = "" + this.state.min;
                                    }
                                    break;
                                case "number":

                                    if (value < this.state.min) {
                                        isvalidate = false;
                                        helpTip = "" + this.state.min;
                                    }
                                    break;
                                case "integer":
                                    if (value < this.state.min) {
                                        isvalidate = false;
                                        helpTip = "" + this.state.min;
                                    }
                                case "checkbox":
                                    var valueArr=value.toString().split(",");
                                    if(valueArr.length<this.state.min)
                                    {
                                        isvalidate=false;
                                        helpTip=""+this.state.min.toString()+"";
                                    }
                                    break;
                                case "select":
                                    var valueArr=value.toString().split(",");
                                    if(valueArr.length<this.state.min)
                                    {
                                        isvalidate=false;
                                        helpTip=""+this.state.min.toString()+"";
                                    }
                                    break;
                            }
                        }
                        if (this.state.max!=null&&this.state.max!=undefined) {
                            switch (this.props.type) {
                                case "text":
                                    if (value.toString().length > this.state.max) {
                                        isvalidate = false;
                                        helpTip = "" + this.state.max;
                                    }
                                    break;
                                case "password":
                                    if (value.toString().length > this.state.max) {
                                        isvalidate = false;
                                        helpTip = "" + this.state.max;
                                    }
                                    break;
                                case "number":
                                    if (value > this.state.max) {
                                        isvalidate = false;
                                        helpTip = "" + this.state.max;
                                    }
                                    break;
                                case "integer":
                                    if (value > this.state.max) {
                                        isvalidate = false;
                                        helpTip = "" + this.state.max;
                                    }
                                    break;
                                case "checkbox":
                                    var valueArr=value.toString().split(",");
                                    if(valueArr.length>this.state.max)
                                    {
                                        isvalidate=false;
                                        helpTip=""+this.state.max.toString()+"";
                                    }
                                    break;
                                case "select":
                                    var valueArr=value.toString().split(",");
                                    if(valueArr.length>this.state.max)
                                    {
                                        isvalidate=false;
                                        helpTip=""+this.state.max.toString()+"";
                                    }
                                    break;
                            }
                        }
                    }


                }
                else {//
                    if (required) {
                        //
                        isvalidate = false;//
                        helpTip = validation["required"];
                    } else {
                        //
                    }

                }
            }

        }


        //
        if(isvalidate) {
            this.setState({
                validateClass: "",
                helpShow:"none",
                helpTip:"",

            })

        }
        else {

            this.setState({
                validateClass: " wasabi-has-error",
                helpShow:"block",
                helpTip:helpTip
            });

        }
        return isvalidate;
    },
}
module .exports=Validate;


