require("../Sass/Form/Input.scss");
var React=require("react");
var regexp=require("../Lang/regs.js");
var validation=require("../Lang/validation.js");
var Radio=require("./Radio.jsx");
var CheckBox=require("./CheckBox.jsx");
var SwitchButton=require("./SwitchButton.jsx");
var ComboBox=require("./ComboBox.jsx");
var Text=require("./Text.jsx");
var None=require("./None.jsx");
var Button=require("../Buttons/Button.jsx");
var LinkButton=require("../Buttons/LinkButton.jsx");
let setStyle=require("../Mixins/setStyle.js");
var unit=require("../libs/unit.js");
var shouldComponentUpdate=require("../Mixins/shouldComponentUpdate.js");
var Input=React.createClass({
    mixins:[setStyle,shouldComponentUpdate],
    propTypes: {
        type:React.PropTypes.oneOf([
            "none",//
            "text",//
            "password",//
            "email",//
            "url",//
            "mobile",//
            "idcard",//
            "date",//
            "time",//
            "datetime",//
            "daterange",//
            "datetimerange",//
            "alpha",//
            "alphanum",//
            "integer",//
            "number",//
            "textarea",//
            "select",//
            "radio",//
            "checkbox",//
            "switch",//
            "picker",//
            "gridpicker",//
            "treepicker",//
            "panelpicker",//
            "muti"//
        ]),//
        name:React.PropTypes.string.isRequired,//
        label:React.PropTypes.oneOfType([React.PropTypes.string,React.PropTypes.element,React.PropTypes.node]),//
        title:React.PropTypes.string,//
        width:React.PropTypes.number,//
        height:React.PropTypes.number,//
        value:React.PropTypes.oneOfType([React.PropTypes.number,React.PropTypes.string]),//,
        text:React.PropTypes.oneOfType([React.PropTypes.number,React.PropTypes.string]),//
        placeholder:React.PropTypes.string,//
        readonly:React.PropTypes.bool,//
        required:React.PropTypes.bool,//
        onlyline:React.PropTypes.bool,//
        hide:React.PropTypes.bool,//
        regexp:React.PropTypes.string,//
        invalidTip:React.PropTypes.string,//
        style:React.PropTypes.object,//style
        controlStyle:React.PropTypes.object,//
        className:React.PropTypes.string,//class
        size:React.PropTypes.oneOf([
            "none",
            "default",
            "large",//,two
            "two",
            "three",
            "onlyline"
        ]),//
        position:React.PropTypes.oneOf([
            "left",
            "default",
            "right"
        ]),//

        // text
        min:React.PropTypes.number,//,,
        max:React.PropTypes.number,//,,
        onClick:React.PropTypes.func,//
        onChange:React.PropTypes.func,//

        // combobox
        multiple:React.PropTypes.bool,//
        valueField: React.PropTypes.string,//
        textField:React.PropTypes.string,//
        url:React.PropTypes.string,//ajax
        params:React.PropTypes.object,//
        dataSource:React.PropTypes.string,//ajax,null
        data:React.PropTypes.array,//
        extraData:React.PropTypes.array,//,url
        onSelect: React.PropTypes.func,//value,text,data

        // picker
        secondUrl:React.PropTypes.string,//,
        secondParams:React.PropTypes.object,//
        secondParamsKey:React.PropTypes.string,//value
        thirdUrl:React.PropTypes.string,//
        thirdParams:React.PropTypes.object,//
        thirdParamsKey:React.PropTypes.string,//value
        hotTitle:React.PropTypes.string,//
        hotData:React.PropTypes.array,//

        //,
        iconCls:React.PropTypes.string,
        iconAlign:React.PropTypes.oneOf([
            "left",
            "right",
            "rightTop"
        ]),//

    },
    getDefaultProps:function() {
        return{
            type:"text",
            name:"",
            label:null,
            title:null,
            width:null,
            height:null,
            value:"",
            text:"",
            placeholder:"",
            readonly:false,
            required:false,
            onlyline:false,
            hide:false,
            regexp:null,
            invalidTip:null,
            style:null,
            controlStyle:null,
            className:null,
            size:"default",
            position:"default",

            //
            row:5,
            min:null,
            max:null,
            onClick:null,
            onChange:null,


            //
            multiple:false,
            valueField:"value",
            textField:"text",
            url:null,
            params:null,
            dataSource:"data",
            data:null,
            extraData:null,
            onSelect:null,

            //
            secondUrl:null,
            secondParams:null,
            secondParamsKey:null,
            thirdUrl:null,
            thirdParams:null,
            thirdParamsKey:null,
            hotTitle:"",
            hotData:null,
        }
    },
    getInitialState:function() {
        return{
            value:this.props.value,
            text:this.props.text,
            readonly:this.props.readonly,
            hide:this.props.hide,
            data:this.props.data,
            //
            required:this.props.required,
            validateClass:"",//
            helpShow:"none",//
            helpTip:validation["required"],//
            invalidTip:"",
        }
    },
    componentWillReceiveProps:function(nextProps) {
        this.setState({
            hide:nextProps.hide,
            value:nextProps.value,
            text: nextProps.text,
            readonly: nextProps.readonly,
            required: nextProps.required,
            data: nextProps.data,
        });
    },
    changeHandler:function(event) {//
        this.setState({
            value: event.target.value,
            text: event.target.value,
        });
        if (this.props.onChange != null) {
            this.props.onChange(event.target.value);//
        }
        //
        if (this.props.backFormHandler != null) {
            this.props.backFormHandler(event.target.value, event.target.value, this.props.name);
        }
    },
    clickHandler:function(event) {//
        if(this.props.onClick!=null) {
            var model = {};
            try {//
                model ={...this.props};
            }
            catch (e) {

            }
            model.value=this.state.value;
            model.text=this.state.text;
            this.props.onClick(this.props.name,this.state.value,model);
        }
    },
    buttonClick:function(name,title,event) {//
        if(this.props.onClick!=null) {
            this.props.onClick(name,title,event);
        }
    },
    validate:function(value) {
        if(this.props.type=="button"||this.props.type=="linkbutton")
        {
            return true;
        }
        else
        {
            return this.refs.input.validate();
        }


    },
    onSelect:function(value,text,name,data) {//
        this.setState({
            value:value,
            text:text,
        });
        if(  this.props.onSelect!=null)
        {
            this.props.onSelect(value,text,this.props.name,data);//
        }
        //
        if (this.props.backFormHandler != null) {
            this.props.backFormHandler(value,text,name,data);
        }

    },
    getComponentData:function(name) {//
        return JSON.parse( window.localStorage.getItem(name+"data"));
    },
    renderText:function() {//
        var props={...this.props}////
        props.value=this.state.value;//
        props.text=this.state.text;//
        return <Text ref="input" {...props} ></Text>
    },
    renderUnInput:function(type) {//
        var control;//
        var props={...this.props}////
        props.value=this.state.value;//
        props.text=this.state.text;//
        if (type=="none")
        {//
            control=<None ref="input" {...props } ></None>
        }
        else  if(type=="radio")
        {//
            control=<Radio ref="input" {...props } onSelect={this.onSelect}></Radio>
        }
        else  if(type=="checkbox")
        {//
            control=<CheckBox ref="input" {...props } onSelect={this.onSelect} ></CheckBox>
        }
        else if(type=="switch")
        {//
            control=<SwitchButton ref="input"  {...props} onSelect={this.onSelect}></SwitchButton>
        }

        else if(type=="muti"||type=="select"||type=="datetime"||type=="time"||type=="date" ||type=="daterange"||type=="datetimerange"||type=="picker"||type=="treepicker"||type=="gridpicker"||type=="panelpicker")
        {//
            control=<ComboBox ref="input" {...props }  onSelect={this.onSelect}></ComboBox>
        }



        return control;
    },
    render:function() {
        var size=this.props.onlyline?"onlyline":this.props.size;
        var componentClassName=  "wasabi-form-group "+size+" "+(this.props.className?this.props.className:"");//
        var style=this.props.style;
        if(this.props.type=="button")
        {

            return <div className={componentClassName} style={style}>   <Button {...this.props} title={this.props.label} onClick={this.buttonClick}></Button></div>
        }
        else if(this.props.type=="linkbutton") {
            return <div className={componentClassName} style={style}><LinkButton {...this.props} title={this.props.label}
                                                                                 onClick={this.buttonClick}></LinkButton></div>
        }
        else
        {
            if(this.props.type=="text"||this.props.type=="email"
                ||this.props.type=="url"||this.props.type=="number"
                ||this.props.type=="integer"||this.props.type=="alpha"
                ||this.props.type=="alphanum"||this.props.type=="mobile"
                ||this.props.type=="idcard"
                ||this.props.type=="password"
                ||this.props.type=="textarea") {//text

                return this.renderText();
            }

            else {//

                return  this.renderUnInput(this.props.type);
            }
        }

        return null;

    }
});
module .exports=Input;


