//desc Input
let React=require("react");


var validation=require("../Lang/validation.js");
let setStyle=require("../Mixins/setStyle.js");
var validate=require("../Mixins/validate.js");
var shouldComponentUpdate=require("../Mixins/shouldComponentUpdate.js");
var Label=require("../Unit/Label.jsx");
var Message=require("../Unit/Message.jsx");
var FetchModel=require("../Model/FetchModel.js");
var unit=require("../libs/unit.js");

var Text=React.createClass({
    mixins:[setStyle,validate,shouldComponentUpdate],
    propTypes: {
        type:React.PropTypes.oneOf([
            "text",//
            "textarea",//
            "password",//
            "email",//
            "url",//
            "mobile",//
            "idcard",//
            "alpha",//
            "alphanum",//
            "integer",//
            "number",//
        ]),//
        name:React.PropTypes.string.isRequired,//
        label:React.PropTypes.oneOfType([React.PropTypes.string,React.PropTypes.element,React.PropTypes.node]),//
        width:React.PropTypes.number,//
        height:React.PropTypes.number,//
        value:React.PropTypes.oneOfType([React.PropTypes.number,React.PropTypes.string]),//,
        text:React.PropTypes.oneOfType([React.PropTypes.number,React.PropTypes.string]),//
        placeholder:React.PropTypes.string,//
        readonly:React.PropTypes.bool,//
        required:React.PropTypes.bool,//
        onlyline:React.PropTypes.bool,//
        hide:React.PropTypes.bool,//
        regexp:React.PropTypes.string,//
        invalidTip:React.PropTypes.string,//
        style:React.PropTypes.object,//style
        className:React.PropTypes.string,//class
        size:React.PropTypes.oneOf([
            "none",
            "default",
            "large",//,two
            "two",
            "three",
            "onlyline"
        ]),//
        position:React.PropTypes.oneOf([
            "left",
            "default",
            "right"
        ]),//

        //
        rows:React.PropTypes.number,//
        min:React.PropTypes.number,//,,
        max:React.PropTypes.number,//,
        onClick:React.PropTypes.func,//
        onChange:React.PropTypes.func,//
        validateUrl:React.PropTypes.string,//url

        onBlur:React.PropTypes.func,//

    },
    getDefaultProps:function() {
        return{
            type:"text",
            name:"",
            label:null,
            width:null,
            height:null,
            value:"",
            text:"",
            placeholder:"",
            readonly:false,
            required:false,
            onlyline:false,
            hide:false,
            regexp:null,
            invalidTip:null,
            style:null,
            className:null,
            size:"default",
            position:"default",

            //
            row:5,
            min:null,
            max:null,
            onClick:null,
            onChange:null,
            validateUrl:null,


        }
    },
    getInitialState:function() {
        return{
            hide:this.props.hide,
            min:this.props.min,
            max:this.props.max,
            value:this.props.value,
            text:this.props.text,
            readonly:this.props.readonly,
            required:this.props.required,
            validateClass:"",//
            helpShow:"none",//
            helpTip:validation["required"],//
            invalidTip:"",
            validateState:null,//

        }
    },
    componentWillReceiveProps:function(nextProps) {
        this.setState({
            hide:nextProps.hide,
            min:nextProps.min,
            max:nextProps.max,
            value: nextProps.value,
            text: nextProps.text,
            readonly: nextProps.readonly,
            required: nextProps.required,
            validateClass:"",//
            helpTip:validation["required"],//
        });

    },
    componentDidMount:function() {
        this.validateInput=true;//
        this.onblur=false;
    },
    componentDidUpdate:function() {
        this.validateInput=true;//
        if(this.state.helpTip==""||this.state.helpTip=="")
        {
            this.refs.input.select();
        }
        if(this.onblur)
        {
            this.onblur=false;
            this.props.onBlur();
        }
    },
    changeHandler:function(event) {
        if (this.validateInput==true) {//

            if((this.props.type=="integer"||this.props.type=="number")) {
                //,
                if(event.target.value=="-"||((!this.state.value||this.state.value.toString().indexOf(".")<0)&&event.target.value.length>0&&event.target.value.toString().lastIndexOf(".")==event.target.value.length-1))
                {
                    //,.
                    this.setState({
                        value: event.target.value,
                        text:event.target.value,
                    });
                    return;
                }




            }



            this.setState({
                value: event.target.value,
                text:event.target.value,
            });

            if (this.props.onChange != null) {
                this.props.onChange(event.target.value);//

            }
            //,onSelect
            if (this.props.backFormHandler != null) {
                this.props.backFormHandler(event.target.value, event.target.value, this.props.name);

            }

        }


    },
    keyDownHandler:function(event) {//
        this.validateInput=true;
        if(this.props.type=="integer"||this.props.type=="number")
        {
            if(((event.ctrlKey==false&&event.metaKey==false)&&event.keyCode>=65&&event.keyCode<=90))
            {//ctrl,command
                this.validateInput=false;
            }

        }
        if(this.props.onKeyDown!=null)
        {
            this.props.onKeyDown(event);
        }

    },
    keyUpHandler:function(event) {
        if(event.keyCode==13)
        {
            if(this.props.validateUrl)
            {
                this.validateHandler(event.target.value);
            }
        }

        if(this.props.onKeyUp!=null)
        {
            this.props.onKeyUp(event);
        }
    },
    focusHandler:function() {//
        if(this.props.onFocus!=null)
        {
            this.props.onFocus();
        }
    },
    blurHandler:function(event) {
        if(this.props.validateUrl) {//
            this.validateHandler(event.target.value);
        }
        else {//
            this.validate(this.state.value);
        }

        this.refs.label.hideHelp();//

        if(this.props.onBlur)
        {
            this.onblur=true

        }

    },
    clickHandler:function(event) {//

        if(this.props.onClick!=null) {
            var model = {};
            try {//
                model ={...this.props};
            }
            catch (e) {

            }
            model.value=this.state.value;
            model.text=this.state.text;
            this.props.onClick(this.props.name,this.state.value,model);
        }
    },

    getValue:function () {//
        return this.state.value;
    },

    validateHandler:function (value) {//
        this.setState({
            validateState:"validing",//
        })
        var fetchmodel=new FetchModel(this.props.validateUrl,this.validateHandlerSuccess,{key:value});
        console.log("text-validing:",fetchmodel);
        unit.fetch.post(fetchmodel);
    },
    validateHandlerSuccess:function () {//
        this.setState({
            validateState:"valid",//
        })
    },
    validateHandlerError:function (errorCode,message) {//
        Message.error(message);
        this.setState({
            validateState:"invalid",//
        })
    },
    render:function() {
        var inputType="text";
        if(this.props.type=="password") {
            inputType = "password";
        }
        var size=this.props.onlyline==true?"onlyline":this.props.size;//
        var componentClassName=  "wasabi-form-group "+size;//
        var style =this.setStyle("input");//
        var controlStyle=this.props.controlStyle?this.props.controlStyle:{};
        controlStyle.display = this.state.hide == true ? "none" : "block";
        let inputProps=
            {
                readOnly:this.state.readonly==true?"readonly":null,
                style:style,
                name:this.props.name,
                placeholder:(this.props.placeholder===""||this.props.placeholder==null)?this.state.required?"":"":this.props.placeholder,
                className:"wasabi-form-control  "+(this.props.className!=null?this.props.className:""),
                rows:this.props.rows,
                title:this.props.title,

            }//
        var control=null;
        if(this.props.type!="textarea")
        {
            control = <input  ref="input" type={inputType}   {...inputProps} onClick={this.clickHandler}
                              onChange={this.changeHandler} onKeyDown={this.keyDownHandler}
                              onKeyUp={this.keyUpHandler} onFocus={this.focusHandler}
                              onBlur={this.blurHandler}
                              value={this.state.value}></input>;
        }
        else {
            //textarea null
            let value=this.state.value;
            if(!value)
            {
                value="";
            }
            control = <textarea ref="input"  {...inputProps} onClick={this.clickHandler}
                                onChange={this.changeHandler} onKeyDown={this.keyDownHandler}
                                onKeyUp={this.keyUpHandler} onFocus={this.focusHandler}
                                onBlur={this.blurHandler}
                                value={value}></textarea>;
        }



        return (<div className={componentClassName+this.state.validateClass} onPaste={this.onPaste} style={ controlStyle}>
                <Label name={this.props.label} ref="label" hide={this.state.hide} required={this.state.required}></Label>
                <div className={ "wasabi-form-group-body"} style={{width:!this.props.label?"100%":null}}>
                    {control}
                    <i className={this.state.validateState} style={{display:(this.state.validateState?"block":"none")}} ></i>
                    <small className={"wasabi-help-block "+this.props.position} style={{display:(this.state.helpTip&&this.state.helpTip!="")?this.state.helpShow:"none"}}>
                        <div className="text">{this.state.helpTip}</div></small>
                </div>
            </div>
        )
    }
});
module .exports=Text;


