let React=require("react");
let showUpdate={
    showUpdate:function(newParam,oldParam) {//
        let isupdate=false;
        if(!oldParam) {
            oldParam = this.state.params;
        }
        if(!newParam&&!oldParam)
        {//
            isupdate=false;//
            return isupdate;
        }
        else if(newParam&&!oldParam&&Object.keys(newParam).length==0)
        {//,,0
            isupdate=false;
            return isupdate;

        }
        else if(newParam&&!oldParam&&Object.keys(newParam).length>0)
        {//,,0
            isupdate=true;
            return isupdate;

        }
        else if(!newParam&&oldParam)
        {//
            isupdate=true;
            return isupdate;

        }
        else if(newParam&&oldParam&&(Object.keys(newParam).length!=Object.keys(oldParam).length))
        {//,
            isupdate=true;
            return isupdate;
        }
        else
        { //,,

            for(var par in newParam)
            {
                try {


                    if (newParam[par] == oldParam[par]) {
                        continue;
                    }
                    else {
                        isupdate = true;
                        return isupdate;
                    }
                }catch (e)
                {
                    isupdate = true;
                    return isupdate;
                }

            }
            

        }

return isupdate;
    },

}
module .exports=showUpdate;


