class FetchModel
{
    constructor(url,success,params=null,error=null,type="GET")
    {
        this.url=url;
        this.params=params;
        this.success=success;
        this.error=error;
        this.type=type;//
        this.contentType="application/x-www-form-urlencoded";////
    }
}
module .exports=FetchModel;


