class  FormModel {
    constructor(name,label="",type = "text",required=false,value="",hide=false,url=null,data=[],extraData=[],valueField="value",textField="text") {
        this.type = type;//
        this.name = name;// key
        this.value=value;///
        this.readonly=false;/// false
        this.onClick=null//
        this.onChange=null,//
        this.className = null; //  className
        this.label = label;//{string|element}  // 
        this.equal = ""; //  Input string   name
        this.min = null ;     //  string  number  array 
        this.max = null ;      //  string  number  array 
        this.required = required; //  false
        this.regexp=null;//
        this.placeholder="";//
        this.onlyline=false;//
        this.rows=5;//textarea
        this.hide=hide;//
        this.text="";//
        this.valueField=valueField;//
        this.textField=textField;//
        this.url=url;//ajax
        this.backSource=null;//ajax,null
        this.data=data;//
        this.onSelect=null;//
        this.extraData=extraData;//
        this.secondUrl=null;//url
        this.thirdUrl=null;//url
        this.params={};//url
        this.secondParams={};//url
        this.secondParamsKey=null;//value
        this.thirdParams={};//url
        this.thirdParamsKey=null;//value
        this.hotTitle=null;//
        this.hotData=null;//
        this.headers=null;//gridpicker
        this.size="default";//
        this.postion="default";//
    }


}

module.exports=FormModel;




