let  React=require("react");
let Modal=require("../Layout/Modal.jsx");
let Button=require("../Buttons/Button.jsx");
let Message=require("../Unit/Message.jsx");
let fileType=require("../libs/fileType.js");
var unit=require("../libs/unit.js");
require("../Sass/Action/Import.scss");
let  Import=React.createClass({
    propTypes: {
        name:React.PropTypes.string,//
        uploadurl: React.PropTypes.string.isRequired,//
        modelurl: React.PropTypes.string.isRequired,//
        failloadurl: React.PropTypes.string.isRequired,//
        importSuccess: React.PropTypes.func//
    },
    getDefaultProps: function () {
        return {
            name:"",
            uploadurl: null,
            failloadurl: "javascript:void(0)",

        }
    },
    getInitialState: function () {
        return {
            name:this.props.name,
            filename: "",//
            uploadDisabled:true,//
            choseDisabled:false,//
            giveupdisabled:true,//
            uploadurl: this.props.uploadurl,
            failloadurl: this.props.failloadurl,
            showfail:false,
            uploadInfo:[],

        }
    },
    componentWillReceiveProps: function (nextProps) {

        this.setState({
            name:nextProps.name,
            uploadurl: nextProps.uploadurl,
            failloadurl: nextProps.failloadurl,
        })
    },
    componentDidUpdate:function()
    {



    },
    close: function () {//
        this.refs.modal.close();
    },
    open: function () {//
        this.setState({
            uploadInfo:[],//
            choseDisabled: false,//
            uploadDisabled:true,//
            giveupdisabled:true,//
            filename:"",//
            showfail:false//
        })
        this.clearFile();//,
        this.refs.modal.open();
    },
    onChange: function (event) {//

        this.isSuccess=false;//
        if(this.state.choseDisabled)
        {
            return ;
        }

        var files=(event.target.files);
        let filename = "";//
        var typevalidate = true;//
        if (files.length > 0) {
            if(files[0].type=="")
            {//
                if((files[0].name.lastIndexOf(".xls")>-1&&files[0].name.lastIndexOf(".xls")+3==files[0].name.length-1)||(files[0].name.lastIndexOf(".xlsx")>-1&&files[0].name.lastIndexOf(".xlsx")+3==files[0].name.length-1))
                {
                    typevalidate = true;
                    filename = files[0].name;
                }
                else
                {
                    typevalidate = false;
                }
            }
            else {
                if (!fileType.isExcel(files[0].type)) {
                    typevalidate = false;
                }
                else {
                    filename = files[0].name;
                }

            }

        }
        else
        {
            typevalidate=false;
        }

        if(typevalidate)
        {
            this.file=files[0];//
            this.setState({
                filename: filename,
                uploadDisabled:false,//
                showfail:false,
            })
        }
        else
        {
            this.file=null;
            this.setState({
                filename: filename,
                uploadDisabled:true,//
                showfail:false,
            })
        }


    },
    importBegin:function(name,title) {//
        //,
        this.isgiveup=false;//,,
        this.total=0;//
        this.failNum=0;//
        this.successNum=0;//
        this.importHandler(null);//
        this.setState({
            choseDisabled: true,//
            uploadDisabled:true,//
            giveupdisabled:false,//
        })

    },
    giveup:function(){//
        this.isgiveup=true;
        let uploadInfo = this.state.uploadInfo;
        uploadInfo.unshift(<div className="info" key={"success"+(index+2).toString()} >{",:"+this.successNum.toString()+",:"+this.failNum.toString()}</div>)
        this.setState({
            uploadInfo:uploadInfo,//
            choseDisabled: false,//
            uploadDisabled:true,//
            giveupdisabled:true,//
            filename:"",//
            showfail:false//
        })
        this.clearFile();//,

    },
    importHandler: function (index) {//
        if(this.isgiveup)
        {
            return ;
        }
        var formData = new FormData(); // 
        if(index==null) {
            //
            if (this.file!=null) {

                if (this.state.uploadurl) {
                    formData.append(this.state.name, this.file);

                }
                else {
                    Message.alert("");
                    return;
                }
            }
        }
        else {
            //
            var image=new Image();
            formData.append(this.state.name, image);
            formData.append("index",index);


        }
        // AJAX
        var xhr = new XMLHttpRequest();
        xhr.upload.addEventListener("progress", this.uploadProgress.bind(this,index), false);//
        xhr.addEventListener("load", this.uploadComplete.bind(this,index), false);
        xhr.addEventListener("error", this.uploadFailed, false);
        xhr.addEventListener("abort", this.uploadCanceled, false);
        xhr.open("POST", this.state.uploadurl, true);
        // 
        xhr.send(formData);

    },
    uploadProgress:function (index,event) {
        if (event.lengthComputable) {
            if(index==null) {//
                var percentComplete = Math.round(event.loaded * 100 / event.total);
                if(percentComplete<100)
                {
                    this.setState({
                        uploadInfo: [<div key={"upload"+percentComplete.toString()} className="success">{"" + percentComplete + "%"}</div>],
                    })
                }
                else
                {
                    this.setState({
                        uploadInfo: [<div key={"upload"+percentComplete.toString()} className="success">{"" + percentComplete + "%,"}</div>],
                    })
                }
            }
        }
        else {
            this.uploadFailed();
        }
    },
    uploadComplete:function (index,event) {
        var xhr= (event.target);
        let uploadInfo = this.state.uploadInfo;
        if ( xhr.readyState == 4 && xhr.status == 200 ) {
            var result=JSON.parse(xhr.responseText);
            if(result&&result.success!=null&&result.success!=undefined) {
                if(result.success==true) {
                    if(index==null)
                    {//
                        this.total=result.data.total; //
                        uploadInfo.unshift(<div key="read" className="success">{","+this.total+",..."}</div>);
                        this.setState({
                            uploadInfo: uploadInfo,
                        });
                    }
                    else {
                        if (result.data.success) {
                            this.successNum+=1;
                            uploadInfo.unshift(<div className="success" key={"success"+(index+2).toString()}>{"" + (index + 2).toString() + ","}</div>)
                        }
                        else {
                            this.failNum+=1;
                            uploadInfo.unshift(
                                <div className="fail" key={"fail"+(index+2).toString()}>{"" + (index + 2).toString() + ",," + result.data.message}</div>)
                        }
                        this.setState({
                            uploadInfo: uploadInfo
                        })
                    }
                    if(index==null)
                    {
                        this.importHandler(0);//
                    }
                    else {

                        if(index>=this.total-1) {//
                            uploadInfo.unshift(<div className="info" key={"successall"} >{",:"+this.successNum.toString()+",:"+this.failNum.toString()}</div>)
                            this.setState({
                                choseDisabled: false,//
                                uploadDisabled:true,//
                                giveupdisabled:true,//
                                filename:"",//
                                showfail:this.failNum>0?true:false,//
                            })
                            this.clearFile();//,

                            if(this.props.importSuccess)
                            {
                                this.props.importSuccess(result);
                            }

                        }
                        else
                        {
                            this.importHandler(index*1+1);//
                        }

                    }
                }
                else {
                    this.clearFile();//,
                    if(index==null) {
                        Message.error(",:"+result.message);
                        this.setState({
                            uploadDisabled:true,//
                            choseDisabled:false,//
                        })

                    }
                    else
                    {
                        Message.error(",,:"+result.message);
                        this.setState({
                            uploadDisabled:true,//
                            choseDisabled:false,//
                        })
                    }

                }
            }
            else {
                this.clearFile();
                Message.error("JSON,,");
                this.setState({

                    uploadDisabled:true,//
                    choseDisabled:false,//
                })

            }

        }
        else {
            this.clearFile();
            if( xhr.statusText.indexOf("404"))
            {
                Message.error(",");
                this.setState({

                    uploadDisabled:true,//
                    choseDisabled:false,//
                })
            }
            else
            {
                Message.error("");
                this.setState({

                    uploadDisabled:true,//
                    choseDisabled:false,//
                })
            }


        }

    },
    uploadFailed:function(event) {
        this.clearFile();
        this.setState({

            choseDisabled: false,//
            uploadDisabled:true,//


        })
        Message.error("");
    },
    uploadCanceled:function uploadCanceled(evt) {
        //
    },
    clearFile:function() {
        try {
            this.refs.import.value="";//,
        }
        catch (e) {

        }
    },
    render:function() {
        let accepts=null;//
        let acceptMap=fileType.getTypeMap("excel");
        if(acceptMap!=null)
        {

            for (let value of acceptMap.values()) {
                if(accepts==null)
                {
                    accepts=value;

                }
                else {
                    accepts+=","+value;
                }
            }
        }
        let props={
            accept:accepts,
            multiple:false,
        }


        return(   <Modal  ref="modal"  width={460} height={340} title="">


            <div className="import-section">
                <input type="text" name={this.state.name} className="import-text" value={this.state.filename} readOnly={true} ></input>
                <input type="file" ref="import" className="import-file" onChange={this.onChange} {...props} style={{display:this.state.choseDisabled?"none":"inline"}}></input>
                <Button type="button" disabled={this.state.choseDisabled} className="import-chose" theme="cancel" title=""></Button>
            </div>

            <div className="import-submit">
                <a className="import-failload" target="blank" href={this.props.failloadurl} style={{display:this.state.showfail==true?"inline":"none"}}></a>
                <Button title="" disabled={this.state.uploadDisabled}  onClick={this.importBegin} theme="green"></Button>
                <Button title="" disabled={this.state.giveupdisabled} onClick={this.giveup} theme="cancel"></Button>
                <Button title=""  onClick={this.close} theme="cancel"></Button>
            </div>
            <a className="import-downloadmodel" href={this.props.modelurl}></a>

            <div className="import-upload-info">{this.state.uploadInfo}</div>
        </Modal>);

    }
});
module .exports=Import;


