class  NodeModel {
    constructor(value, text, isParent = false) {
        this.value = value;//
        this.text = text;//
        this.isParent = isParent;//,
        this.tip = null;//
        this.iconCls = "icon-file";//
        this.iconClose = "icon-folder";//
        this.iconOpen = "icon-open-folder";//
        this.open = false;//
        this.selected = false;//
        this.checked = false,//
        this.checkAble = false;//
        this.checkedType = {"Y": "ps", "N": "ps"},//
        this.href = "javascript=void(0)";//
        this.url = null;//url
        this.key = "id";//
        this.params = null;//
        this.property = null;////,TODO 
        this.data = [];////
        this.onSelect = null;////

    }
}
module.exports=NodeModel;


