let React = require("react");
let Message = require("../../Unit/Message.jsx");
let unit = require("../../libs/unit.js");
let FetchModel = require("../../Model/FetchModel.js");
let PageHandlerMixins = {
    getHandler: function (id, disabled) {//
        var getUrl = this.state.getUrl;
        if (getUrl.indexOf("?") > 0) {//

            getUrl = getUrl + "&id=" + id;
        }
        else {
            getUrl = getUrl + "?id=" + id;
        }
        let fetchModel = new FetchModel(getUrl, this.getSuccess.bind(disabled), null, this.fetchErrorHandler);
        unit.fetch.get(fetchModel);
    },
    getSuccess: function (result, disabled) {//
        if (result.data) {
            var model = this.state.model;
            for (var index = 0; index < model.length; index++) {
                if (result.data[model[index].name]) {
                    model[index].value = result.data[model[index].name];
                }
                else {
                    model[index].value = null;//
                }
            }
            this.setState({
                model: model,
                disabled: false,//
                submitButton: this.submitButton(disabled),//
            });
        }
    },
    addOpen: function () {
        this.refs.slide.open();
         this.state.model = this.state.model.map((item, index) => {
                item.value ="";
                return item;
            }
         );
         console.log(this.state.model);
        this.setState({
            panelTitle: "",
            opType: "add",
            model:this.state.model
        })
    },
    addHandler: function (model) {//
        let fetchModel = new FetchModel(this.state.addUrl, this.addSuccess, model, this.fetchErrorHandler);
        unit.fetch.post(fetchModel);
    },
    addSuccess: function (result) {//
        this.refs.datagrid.reload();//
    },
    deleteHandler: function (id) {//
        Message.confirm("?", () => {
            var deleteUrl = this.state.deleteUrl;
            if (deleteUrl.indexOf("?") > 0) {//

                deleteUrl = deleteUrl + "&id=" + id;
            }
            else {
                deleteUrl = deleteUrl + "?id=" + id;
            }
            let fetchModel = new FetchModel(deleteUrl, this.deleteSuccess, null, this.fetchErrorHandler);
            unit.fetch.get(fetchModel);
        });

    },
    deleteSuccess: function (result) {//
        this.refs.datagrid.reload();//
    },
    updateOpen: function () {
        let model = this.refs.datagrid.getFocusRowData();
        if (model) {
             this.refs.slide.open();
            this.state.model = this.state.model.map((item, index) => {
                item.value = model[item.name];
                return item;
            }
            )
            this.setState({
                panelTitle: "",
                opType: "update",
                model: this.state.model,
            })
        } else {
            Message.alert("");
        }

    },
    updateHandler: function (model) {//
        let fetchModel = new FetchModel(this.state.updateUrl, this.addSuccess, model, this.fetchErrorHandler);
        unit.fetch.post(fetchModel);
    },
    updateSuccess: function (result) {//
        this.refs.datagrid.reload();//
    },
    filterHandler: function (params) {//
        this.refs.datagrid.reload(params);
    },
    openSlideHandler: function (type, id) {//
        this.refs.form.clearData();//
        switch (type) {
            case "add":
                //
                this.setState({
                    disabled: false,//
                    submitButton: this.submitButton(false),//
                });
                break;
            case "update":
                //
                this.getHandler(id, false);//
                break;
            case "search":
                //
                this.getHandler(id, true);//
                break;
        }
    },
    submitHandler: function () {//
        var model = this.refs.form.getData();//
        switch (this.state.opType) {
            case "add":
                this.addHandler(model);
                break;
            case "update":
                this.updateHandler(model);
                break;
        }
    },
    btnHandler: function (name, title) {
        if (name == "add") {
            this.addOpen();
        } else if (name == "update") {
            this.updateOpen();
        }
    },
    fetchErrorHandler: function (errorCode, errorMssage) {//
        console.log(errorCode, errorMssage);
        Message.error("" + errorMssage);
    },
}
module.exports = PageHandlerMixins;


